
.main {
	background: #eee url(../img/bg.jpg) center;
	background-size: cover;
	height: 100%;
	width: 100%; }

.wrapper_white {
	box-shadow: 0 5px 30px 5px #000;
	background-color: #FFF;
	@include clr; }

.wrapper__left {
	float: left;
	max-width: 75%;
	height: 100%; }

.wrapper__right {
	position: relative;
	float: left;
	max-width: 25%;
	height: 100%;
	&:before {
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		display: block;
		height: 100%;
		width: 1px;
		background-color: #ececee; } }

.download__more {
	width: 100%;
	height: 53px;
	background-image: linear-gradient(#ddd 0%, #eee 30%, #fff 80%);
	text-align: center;
	&:hover {
		cursor: pointer;
		background-image: linear-gradient(#ddd 0%, #eee 30%, #fff 50%,#eee 80%, #ddd 100%); }

	.download__text {
		display: inline-block;
		line-height: 53px;
		color: #b3b3b3;
		font-size: 15px;
		font-weight: 700;
		text-transform: uppercase;
		position: relative;
		&:after {
			content: "";
			display: block;
			position: absolute;
			top: 15px;
			right: -35px;
			width: 22px;
			height: 22px;
			background: transparent url(../img/icon-more.png) no-repeat center; } } }
