.slider__wrapper {
	width: 100%;
	height: 400px;
 }	// overflow: hidden

.slider {
	// width: 100%
	// max-height: 100%
	// // position: relative
	&:before {
		content: "";
		// position: absolute
		width: 100%;
		height: 28px;
		background-image: linear-gradient(#000 20%, transparent 50%);
		opacity: 0.1;
		z-index: 2; }

	.slider__item {
		width: 100%;
		max-height: 100%;
		position: relative;

		background: url(../img/top_slider_bg.png) no-repeat center;
		@include cover;
		// img
		// 	width: 100%
		// 	height: auto

		.slider__item_blind {
			position: absolute;
			top: 0;
			left: 46.5%;
			width: 365px;
			height: 100%;
			opacity: 0.4;
			background-color: #fdd275;
			-webkit-transform: skewX(-25deg);
			-moz-transform: skewX(-25deg);
			-ms-transform: skewX(-25deg);
			-o-transform: skewX(-25deg);
			transform: skewX(-25deg); }

		.slider__head {
			position: absolute;
			top: 78px;
			left: 57%;
			height: 40px;
			padding: 0 13px;
			background-color: #fdd275;
			color: white;
			font-size: 35px;
			font-weight: 300;
			line-height: 40px;
			text-transform: uppercase; }

		.slider__text {
			position: absolute;
			top: 118px;
			left: 57%;
			height: 40px;
			padding: 0 13px;
			background-color: white;
			color: #38393d;
			font-size: 15px;
			font-weight: 400;
			line-height: 40px; } }

	.slick-prev {
		cursor: pointer;
		position: absolute;
		z-index: 10;
		top: 45%;
		left: 0;
		width: 41px;
		height: 71px;
		color: transparent;
		background-color: #FFF;
		-webkit-border-radius: 0 6px 6px 0;
		border-radius: 0 6px 6px 0;
		&:before, &:after {
			content: "";
			display: block;
			width: 15px;
			height: 0;
			border: 2px solid #a1a1a1;
			border-radius: 10px;
			transform: rotate(-45deg) translate(10px, 6px); }
		&:after {
			transform: rotate(45deg) translate(10px, -6px); } }
	.slick-next {
		@extend .slick-prev;
		transform: rotate(180deg);
		left: auto;
		right: 0; } }

.slick-prev:hover, .slick-next:hover, .slick-prev:focus, .slick-next:focus {
	background-color: rgba(0,0,0,0.2); }

